body{
    margin: 0;
    padding: 0;
}
body *{
    margin: 0;
    padding: 0;
}
/* nav-bar */
.navBar{
    width: 100%;
    background: #433442;
    color: #FFF;
    padding: 20px;
    font-size: 20px;
    display: flex;
}
.navBar .navItem{
    pointer-events: none;
}
.main{
    position: relative; 
}
.list{
    display: block;
    width: 100%;
    padding: 20px;
    position: relative;
    border-bottom: 1px solid #000;
}
.list h3{
    position: relative;
    display: block;
    width: 100%;
}
.list p{
    position: relative;
    display: block;
    width: 100%;
}
.modal{
   width: 100%;
    padding:40px 0px;
    background: #CCC;
    text-align: center;
}